<template>
  <div id="web">
    <div class="home-title">
      <h2><Icon type="ios-book" />前端文章</h2>
    </div>
    <div class="nav">
      <span v-for="(val, i) in nav" :class="{active:val.status}" @click="tabNav(val.text)">
        {{val.text}}
        <i>{{i===nav.length-1? '':'/'}}</i>
      </span>
    </div>
    <div class="showdata">
      <article-list :param="toChild"></article-list>
    </div>
  </div>
</template>

<script>
  import articleList from './articleList';
    export default {
      components: {articleList},
      name: "web",
      data(){
        return{
          nav:[
            {
              text:"Javascript",
              status:true
            },{
              text:"Css",
              status:false
            },{
              text:"Html",
              status:false
            },
          ],
          toChild:{
            login:this.outdata, //来自父组件
            flag: "js",
          },
        }
      },
      props:['outdata'],
      mounted(){
      },
      methods:{
        tabNav(text){
          let that = this;
          this.nav.forEach(function (ele) {
            ele.status = false;
            if(text === ele.text){
              ele.status = true;
              if(text==="Javascript"){
                that.toChild.flag = 'js'
              }else{
                that.toChild.flag = text
              }
            }
          })
        },
      }
    }
</script>

<style scoped lang="less">
  /*start less类*/
  .xflex(@num){
    flex: @num;
    -webkit-flex: @num;
    -ms-flex: @num;
    -moz-box-flex: @num;
  }
  .xdisplayflex(){
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex
  }
  .xboxshow(){
    -moz-box-shadow:1px 1px 5px #515151;
    -webkit-box-shadow:1px 1px 5px #515151;
    box-shadow:1px 1px 5px #515151;
  }
  .bgimg(@path){
    background-image: url(@path);
    -moz-background-size:cover; /* 老版本的 Firefox */
    background-repeat:no-repeat;
    background-size: cover;
    background-position: center center;
  }
  .h2(){
    h2{
      border-bottom: 1px solid rgba(83, 83, 83, 0.15);
    }
    h2::after{
      content: "";
      display: block;
      width: 18%;
      border: 1px solid rgb(0, 162, 255);
      vertical-align: middle;
    }
  }
  @color: rgb(179, 179, 179);
  @textcolor: #0097ee;
  /*end less类*/
  .active{
    color: #000 !important;
  }
  @media (max-width: 750px){
    #web{
      width: 90%;
      margin: 0 auto;
      .home-title{
        text-align: left;
        .h2();
      }
      .nav{
        text-align: right;
        font-size: 16/32rem;
        margin: 10/32rem 0;
        color: @color;
        span{
          margin: 0 3/32rem;
        }
        span:hover{
          cursor: pointer;
        }
      }
    }
  }
  @media (min-width: 750px){
    #web{
      .home-title{
        text-align: left;
        .h2();
      }
      .nav{
        text-align: right;
        font-size: 16px;
        margin: 10px 0;
        color: @color;
        span{
          margin: 0 3px;
        }
        span:hover{
          cursor: pointer;
        }
      }
    }
  }
</style>



